      _cache[6] || (_cache[6] = _createStaticVNode("<section data-v-2c6389db><h2 data-v-2c6389db>2. Hinweis zur Aktualität und Geltungsbereich</h2><p data-v-2c6389db> Diese Datenschutzerklärung gilt für die Web-Präsenz unter dieser Domain. Obwohl die Website derzeit keine personenbezogenen Daten erhebt, beschreibt diese Datenschutzerklärung detailliert, wie personenbezogene Daten verarbeitet würden, falls Funktionen (z. B. Kontaktformulare, Terminvereinbarung, Analyse-Tools) aktiviert werden. </p></section><section data-v-2c6389db><h2 data-v-2c6389db>3. Begriffsbestimmungen</h2><p data-v-2c6389db> Personenbezogene Daten sind alle Informationen, die sich auf eine identifizierte oder identifizierbare natürliche Person beziehen (z. B. Name, E-Mail, Telefonnummer, IP-Adresse). Verarbeitung umfasst jeden Umgang mit personenbezogenen Daten. </p></section><section data-v-2c6389db><h2 data-v-2c6389db>4. Zweck(e) und Rechtsgrundlagen der Verarbeitung</h2><ul data-v-2c6389db><li data-v-2c6389db><strong data-v-2c6389db>Bereitstellung und Betrieb der Website</strong><br data-v-2c6389db> Zwecke: Darstellung von Inhalten, Erhaltung der Verfügbarkeit der Seite.<br data-v-2c6389db> Rechtsgrundlage: berechtigtes Interesse (Art. 6 Abs. 1 lit. f DSGVO). </li><li data-v-2c6389db><strong data-v-2c6389db>Kontaktaufnahme / Terminvereinbarung</strong><br data-v-2c6389db> Zwecke: Beantwortung Ihrer Anfragen, organisatorische Bearbeitung von Terminanfragen.<br data-v-2c6389db> Rechtsgrundlage: Einwilligung (Art. 6 Abs. 1 lit. a DSGVO) bzw. Erfüllung vorvertraglicher Maßnahmen (Art. 6 Abs. 1 lit. b). </li><li data-v-2c6389db><strong data-v-2c6389db>Analyse und Performance (z. B. Web-Analytics)</strong><br data-v-2c6389db> Zwecke: Verbesserung des Angebots, Nutzungsanalyse.<br data-v-2c6389db> Rechtsgrundlage: in der Regel Einwilligung (Art. 6 Abs. 1 lit. a) oder berechtigtes Interesse (Art. 6 Abs. 1 lit. f) mit Abwägung. </li><li data-v-2c6389db><strong data-v-2c6389db>Verwendung von Drittanbieterdiensten (z. B. Google Maps)</strong><br data-v-2c6389db> Zwecke: Einbettung interaktiver Karten, Anzeige Standort.<br data-v-2c6389db> Rechtsgrundlage: Einwilligung oder berechtigtes Interesse; ggf. besondere Hinweise weiter unten. </li></ul></section><section data-v-2c6389db><h2 data-v-2c6389db>5. Kategorien personenbezogener Daten</h2><p data-v-2c6389db> Je nach Nutzung der Website und Diensten können verarbeitet werden: </p><ul data-v-2c6389db><li data-v-2c6389db>Bestandsdaten (z. B. Name, Anschrift)</li><li data-v-2c6389db>Kontaktdaten (E-Mail, Telefonnummer)</li><li data-v-2c6389db>Inhaltsdaten (z. B. Nachrichten, Formularangaben)</li><li data-v-2c6389db>Nutzungsdaten (z. B. besuchte Seiten, Zugriffszeiten)</li><li data-v-2c6389db>Meta-/Kommunikationsdaten (z. B. IP-Adresse, Browserdaten)</li></ul></section><section data-v-2c6389db><h2 data-v-2c6389db>6. Cookies und ähnliche Technologien</h2><p data-v-2c6389db> Wir verwenden Cookies, um die Funktionalität der Website sicherzustellen und Nutzungsdaten zu analysieren. Cookies sind kleine Textdateien, die im Browser gespeichert werden. </p><p data-v-2c6389db> Sie können Ihre Browser-Einstellungen so anpassen, dass Sie über das Setzen von Cookies informiert werden und Cookies nur im Einzelfall erlauben oder generell ausschließen. Bei Deaktivierung von Cookies kann die Funktionalität der Website eingeschränkt sein. </p></section><section data-v-2c6389db><h2 data-v-2c6389db>7. Web-Analytics</h2><p data-v-2c6389db> Sofern Web-Analytics-Dienste (z. B. Google Analytics) eingesetzt werden, erfolgt dies nur mit Ihrer Einwilligung oder unter strengen Maßnahmen zur Anonymisierung/Datensparsamkeit. Diese Dienste können Cookies verwenden und Daten an den jeweiligen Provider übertragen. </p><p data-v-2c6389db> Falls Google Analytics verwendet wird, können Sie die Erfassung Ihrer Daten durch ein Browser-Add-on deaktivieren oder die Datenschutzhinweise des Anbieters nutzen. </p></section><section data-v-2c6389db><h2 data-v-2c6389db>8. Einbindung von Diensten Dritter</h2><p data-v-2c6389db> Wir binden ggf. Inhalte oder Funktionen Dritter ein (z. B. Google Maps zur Anzeige eines Standortes). Dabei können Daten an die Drittanbieter übermittelt werden. Bitte beachten Sie die Datenschutzhinweise der jeweiligen Anbieter. </p></section><section data-v-2c6389db><h2 data-v-2c6389db>9. Speicherung und Löschung von Daten</h2><p data-v-2c6389db> Personenbezogene Daten werden nur so lange gespeichert, wie es für die Zwecke erforderlich ist oder gesetzliche Aufbewahrungsfristen dies vorsehen. Nach Wegfall des Zwecks werden die Daten gelöscht oder anonymisiert. </p></section><section data-v-2c6389db><h2 data-v-2c6389db>10. Sicherheitsmaßnahmen</h2><p data-v-2c6389db> Wir treffen technische und organisatorische Maßnahmen, um personenbezogene Daten vor Verlust, Missbrauch oder unberechtigtem Zugriff zu schützen (z. B. Verschlüsselung, Zutritts- und Zugriffsschutz). Vollständige Sicherheit kann jedoch nicht garantiert werden. </p></section><section data-v-2c6389db><h2 data-v-2c6389db>11. Übermittlung in Drittstaaten</h2><p data-v-2c6389db> Eine Übermittlung personenbezogener Daten in Staaten außerhalb der EU/des EWR erfolgt nur, wenn geeignete Garantien bestehen (z. B. EU-Standardvertragsklauseln) oder gesetzliche Ausnahmen greifen. Konkrete Hinweise finden Sie bei den jeweiligen Diensten (z. B. Google). </p></section><section data-v-2c6389db><h2 data-v-2c6389db>12. Rechte der betroffenen Personen</h2><p data-v-2c6389db> Sie haben folgende Rechte hinsichtlich Ihrer bei uns gespeicherten personenbezogenen Daten: </p><ul data-v-2c6389db><li data-v-2c6389db>Recht auf Auskunft</li><li data-v-2c6389db>Recht auf Berichtigung</li><li data-v-2c6389db>Recht auf Löschung (Recht auf Vergessenwerden)</li><li data-v-2c6389db>Recht auf Einschränkung der Verarbeitung</li><li data-v-2c6389db>Recht auf Datenübertragbarkeit</li><li data-v-2c6389db>Recht auf Widerruf erteilter Einwilligungen</li><li data-v-2c6389db>Recht auf Beschwerde bei einer Aufsichtsbehörde</li></ul><p data-v-2c6389db> Zur Ausübung dieser Rechte kontaktieren Sie uns bitte unter der oben angegebenen Adresse oder per E-Mail. </p></section><section data-v-2c6389db><h2 data-v-2c6389db>13. Widerruf und Widerspruch</h2><p data-v-2c6389db> Ein erteilter Einwilligung zur Datenverarbeitung kann jederzeit mit Wirkung für die Zukunft widerrufen werden. Gegen Verarbeitung, die auf berechtigten Interessen beruht, können Sie jederzeit Widerspruch einlegen. </p></section><section data-v-2c6389db><h2 data-v-2c6389db>14. Änderungen dieser Datenschutzerklärung</h2><p data-v-2c6389db> Diese Datenschutzerklärung kann von Zeit zu Zeit angepasst werden. Die jeweils aktuelle Version ist auf dieser Website verfügbar. Maßgeblich ist die jeweils veröffentlichte Fassung. </p></section>", 13)),
